$color-main: #0056A9;
$color-red: #C23349;
$color-green: #5CAB5F;
$color-dark-blue: #32449F;
$color-grey: #525252;
$color-dark-grey: #474747;
$color-white: #ececec;


body {
    background: black; }

.main-section {
    min-height: 750px;
    background: url("../images/193.png"), $color-main 50%;
    background-repeat: no-repeat;
    background-blend-mode: screen;
    display: flex;
    align-items: center;
    &__container {
        width: 500px;
        margin: 0 auto;
        background-color: #043b6f;//rgb(131, 131, 131)
        border-radius: 6px;
        box-shadow: 8px 5px 4px 0px rgba(0, 0, 0, 0.25); }
    &__message {
        padding: 30px;
        color: #ffffff;
        font-size: 20px;
        span {
            margin-left: 20px; } }
    &__title {
        font-size: 26px;
        text-align: center; }
    &__date {
        text-align: center; }
    &__services {
        font-size: 22px; } }

@media (max-width: 1199px) {
    .main-section {
        min-height: 650px;
        &__container {
            width: 400px; }
        &__message {
            span {
                font-size: 18px; } }
        &__title {
            font-size: 22px; }
        &__date {
            font-size: 20px; }
        &__services {
            font-size: 20px; } } }

@media (max-width: 991px) {
    .main-section {
        min-height: 570px;
        &__container {
            width: 300px; }
        &__message {
            font-size: 18px;
            padding: 15px;
            span {
                font-size: 16px; } }
        &__title {
            font-size: 20px; }
        &__date {
            font-size: 18px; }
        &__services {
            font-size: 18px; } } }

@media (max-width: 767px) {
    .main-section {
        // min-height: 460px
        &__container {
            width: 250px; }
        &__message {
            font-size: 16px;
            span {
                font-size: 16px; } }
        &__title {
            font-size: 20px; }
        &__date {
            font-size: 18px; }
        &__services {
            font-size: 16px; } } }

@media (max-width: 575px) {
    .main-section {
        &__container {
            width: 220px; }
        &__message {
            font-size: 14px;
            span {
                font-size: 14px; } }
        &__title {
            font-size: 18px; }
        &__date {
            font-size: 16px; }
        &__services {
            font-size: 14px; } } }

